"use client";
import { CommentType } from "@/types/types";
import { format} from "date-fns";

export default function ShowComment( { comment}:{ comment:CommentType}){

    return(
        <div className="border-2 border-red-300 py-3 px-2">
            <div className="font-semibold">{comment?.content}</div>
            {/* <div className="text-sm">{ format(new Date(comment?.created_at?.toDateString()), "eee PPP") }</div> */}
            <div></div>
        </div>
    )
}